
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
components: {
ViewBox,
XHeader,
Tab,
TabItem,
Scroller
},
data () {
return {
index: 0,
isSystemInform: true,
systemInform: [],
userInform: [],
pulldownConfig: {
content: '下拉刷新',
height: 60,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放刷新',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pulldown-'
}
}
},
mounted () {
this.getInformList('0')
},
methods: {
onPulldownLoading () {
let self = this
self.isSystemInform ? this.getInformList('0') : this.getInformList('1')
self.$nextTick(() => {

self.$refs.informScroller.donePulldown()
self.$refs.informScroller.reset({
top: 0
})
})
},
onItemClick (index) {
index === '0' ? this.isSystemInform = true : this.isSystemInform = false
this.getInformList(index)
},
getInformList (type) {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/getAppNoticeList.do'
let data = {
path: path,
params: {
type: type
}
}
self.$store.dispatch('normalRequest', data).then(res => {
if (res.response === 'success') {
type === '0' ? self.systemInform = res.data : self.userInform = res.data
} else {
this.$vux.toast.text(res.data)
}
})
}
}
}



